.Header{
     position: fixed;
     top: 0;
     width: 100%;
     height: 30px;
     padding: 20px;
     display: flex;
     justify-content: space-between;
     
}
.logo{
    position: fixed;
    left: 20px;
    width: 90px;
    cursor: pointer;
}
.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    cursor: pointer;
}