.row{
    margin-left: 20px;
    color: #fff;

}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
    cursor: pointer;
}
.posters::-webkit-scrollbar{
display: none;
}
.poster{
    max-width: 350px;
    margin-right: 10px;
}

.sposter{
    max-height: 150px;
    margin-right: 10px;
}
.poster:hover{
    transform: scale(1.1);
}
.sposter:hover{
    transform: scale(1.1);
}